.tree {
	background: #444;
	border: 2px solid #444;
	border-radius: 6px;
	margin: 2px;
	display: inline-block;
	width: 240px;
}

.treeHeader {
	color: #fff;
	font-size: 16px;
	padding: 6px;
	position: relative;
	text-align: center;
}

.treeHeaderText {
	width: fit-content;
	margin: 0 auto;
}

.grid {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	grid-template-rows: repeat(7, 1fr);
	height: 420px;
	width: 240px;
	background-size: 100% 100%;
	border-radius: 4px;
	position: relative;
}

.reset {
	position: absolute;
	right: 0.5rem;
	top: 0.1rem;
	color: #c0392b;
}

.reset:hover {
	cursor: pointer;
}
